    /**
     * 顶部功能
     * @param value
     */
    private headFunc(value: number) {
        if (value === 1) {
            Command.undo();
        }
        if (value === 2) {
            Command.redo();
        }
        if (value === 3) {
            if (!this.width || !this.height) {
                return
            }
            /**
             * merge map data
             */
            LaserMap.instance.customMerge.addCustomMergeUI();
        }
    }

    /**
     * 绘制标尺数据
     */
    private renderRuler() {
        // @ts-ignore
        this.rulerTool.rulerG.clear();
        const rulerData = this.rulerTool.data;
        if (rulerData.length < 1) return;
        let { rx, ry } = this.laserToRelativeCoordinate(...rulerData[0]);
        rx = _.round(rx * this.scale, PRECISION);
        ry = _.round(ry * this.scale, PRECISION);
        // @ts-ignore
        this.rulerTool.rulerG.text(0 + ' m').fill('#cccccc').font({
            size: 12,
            anchor: '' +
                'middle'
        }).move(rx + 4, ry + 4);
        if (rulerData.length > 1) {
            rulerData.forEach((l, index, arr) => {
                if (index > 0) {
                    let { rx: x1, ry: y1 } = this.laserToRelativeCoordinate(...arr[index - 1]);
                    let { rx: x2, ry: y2 } = this.laserToRelativeCoordinate(...l);
                    x1 = _.round(x1 * this.scale, PRECISION);
                    y1 = _.round(y1 * this.scale, PRECISION);
                    x2 = _.round(x2 * this.scale, PRECISION);
                    y2 = _.round(y2 * this.scale, PRECISION);
                    // @ts-ignore
                    this.rulerTool.rulerG.polyline(`${x1}, ${y1} ${x2},${y2}`).stroke({ width: 2, color: '#ff0' });
                    /**
                     * 求已确定线段的总长度
                     */
                    const len = arr.slice(0, index + 1)
                        .reduce((accum, _l, _index, _arr) => {
                            if (_index > 0) {
                                return _.round(accum + lineLength([..._arr[_index - 1], ..._l]), PRECISION)
                            } else {
                                return 0
                            }
                        }, 0);
                    // @ts-ignore
                    this.rulerTool.rulerG.text(len + ' m').fill('#cccccc').font({
                        size: 12,
                        anchor: '' +
                            'middle'
                    }).move(x2 + 4, y2 - 20);
                }
            });
        }
    }

