  @Watch("propForm.propertyTemplate", { deep: true })
  handlePropForm(newV: any, oldV: any) {
    this.propForm.propertyTemplate = this.handleUndefinedValue(
      this.propForm.propertyTemplate
    );
    if (!newV || !oldV) {
      return;
    }
    // 更新点 和 路径
    if (this.extendUser === "spot") {
      if (newV.refId === oldV.refId) {
        let spot = this.Topo?.spotsMapper.get(newV.refId);
        // 保证code唯一性
        if (spot.code !== newV.code) {
          spot.code = this.Topo.checkSpotCodeRepeat(newV.code);
          newV.code = spot.code;
        }
        newV.dir = handleAngleRange(newV.dir);
        newV.offsetTheta = handleAngleRange(newV.offsetTheta);
        newV.property2Theta = handleAngleRange(newV.property2Theta);
        newV.property3Theta = handleAngleRange(newV.property3Theta);
        newV._x = _.round(newV._x, PRECISION);
        newV._y = _.round(newV._y, PRECISION);
        newV.offsetX = _.round(newV.offsetX, PRECISION);
        newV.offsetY = _.round(newV.offsetY, PRECISION);
        newV.property2X = _.round(newV.property2X, PRECISION);
        newV.property2Y = _.round(newV.property2Y, PRECISION);
        newV.property3X = _.round(newV.property3X, PRECISION);
        newV.property3Y = _.round(newV.property3Y, PRECISION);
        Object.assign(spot, newV);
        //@ts-ignore
        spot.type = newV["type"][0];
        //@ts-ignore
        spot.orient = newV["orient"][0];
        // @ts-ignore
        spot["property4"] = Number(newV["property4"]); //将布尔值转化为数字，true-->1
        // @ts-ignore
        spot["property5"] = Number(newV["property5"]);
        // @ts-ignore
        spot["layingOff"] = Number(newV["layingOff"]);
        // @ts-ignore
        spot["isTraffic"] = Number(newV["isTraffic"]);
        // @ts-ignore
        spot["isForbidden"] = Number(newV["isForbidden"]);
        //@ts-ignore
        this.Topo.updateSpot(spot, true);
      }
    } else {
      if (newV.refId === oldV.refId) {
        let route = this.Topo?.routesMapper.get(
          `${newV.startSpotRefId}-${newV.endSpotRefId}`
        );
        newV.maxSpeed = _.round(newV.maxSpeed, PRECISION);
        newV.securityDist = _.round(newV.securityDist, PRECISION);
        newV.avoidanceMode = _.round(newV.avoidanceMode, 0);
        newV.expansionMode = _.round(newV.expansionMode, 0);
        Object.assign(route, newV);
        // @ts-ignore
        route["pointMode"] = newV["pointMode"][0];
        // @ts-ignore
        route["property2"] = newV["property2"][0];
        // @ts-ignore
        route["property3"] = newV["property3"][0];
        // @ts-ignore
        route["isAvoidance"] = Number(newV["isAvoidance"]);
        route.property5 = Number(newV["property5"]);
        route.fullWeight = Number(newV["fullWeight"]);
        //@ts-ignore
        // this.Topo.updateSpot(route, true);
      }
    }
  }
  @Watch("form.predefineTemplate", { deep: true })
  handleForm() {
    this.form.predefineTemplate = this.handleUndefinedValue(
      this.form.predefineTemplate
    );
  }
  @Watch("extendForm", { deep: true })
  handleExtendForm() {
    if (!this.propForm.propertyTemplate) return;
    let json = JSON.parse(JSON.stringify(this.extendForm));
    if (this.extendUser === "spot") {
      let spot = this.Topo?.spotsMapper.get(
        this.propForm.propertyTemplate.refId
      );
      spot.extend = JSON.stringify(json);
      this.Topo.updateSpot(spot, true);
    } else {
      let key = `${this.propForm.propertyTemplate.startSpotRefId}-${this.propForm.propertyTemplate.endSpotRefId}`;
      let route = this.Topo?.routesMapper.get(key);
      route.extend = JSON.stringify(json);
      this.propForm.propertyTemplate.extend = JSON.stringify(json);
    }
  }

